@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');

.authButton {
  margin: auto;
  width: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;

}

.title {
  margin-top: 30%;
  color: white;
  font-family: 'Pacifico', cursive;
}

.frontpage {
  height: 100vh;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  background: -webkit-linear-gradient(253deg, #bb86fc, #3700b3, #03dac6);
  background: -o-linear-gradient(253deg, #bb86fc, #3700b3, #03dac6);
  background: linear-gradient(197deg, #bb86fc, #3700b3, #03dac6);
  background-size: 600% 600%;

  -webkit-animation: AnimationName 9s ease infinite;
  animation: AnimationName 9s ease infinite;
}

@-webkit-keyframes AnimationName {
  0%{background-position:17% 0%}
  50%{background-position:84% 100%}
  100%{background-position:17% 0%}
}
@keyframes AnimationName {
  0%{background-position:17% 0%}
  50%{background-position:84% 100%}
  100%{background-position:17% 0%}
}



